import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { KlaimComponent } from './klaim/klaim.component';
import { HealthcareComponent } from './healthcare.component';
import { BpjsComponent } from './bpjs/bpjs.component';
import { DetailKlaimComponent } from './detail-klaim/detail-klaim.component';


const routes: Routes = [
    {
        path: '',
        component: HealthcareComponent,
        children: [
            {
                path: 'klaim',
                component: KlaimComponent
            },
            {
                path: 'detail-klaim/:id_klaim_pengajuan',
                component: DetailKlaimComponent
            },
            {
                path: 'bpjs/:jenis_bpjs',
                component: BpjsComponent
            },
        ]
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HealthcareRoutingModule { }
