import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { HealthcareRoutingModule } from './healthcare-routing.module';
import { HealthcareComponent } from './healthcare.component';
import { KlaimComponent } from './klaim/klaim.component';
import { NgZorroAntdModule, NzLayoutModule, NzRadioModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { DocumentModule } from 'src/app/components/document/document.module';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { LayoutModule } from 'src/app/components/layout/layout.module';
import { BpjsComponent } from './bpjs/bpjs.component';
