import { Component, OnInit } from '@angular/core';
import { PayrollPegawaiBpjsService } from 'src/app/sdk/payroll/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
    selector: 'app-bpjs',
    templateUrl: './bpjs.component.html',
    styleUrls: ['./bpjs.component.scss']
})
export class BpjsComponent implements OnInit {

    constructor(
        private payrollPegawaiBpjsService: PayrollPegawaiBpjsService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private activeRoute: ActivatedRoute,
        private router: Router
    ) { }



    jenis_bpjs = '';
    ngOnInit() {

        this.activeRoute.paramMap.subscribe(
            data => {
                if (data.get('jenis_bpjs')) {
                    this.jenis_bpjs = data.get('jenis_bpjs');
                    this.searchData()
                }
            }
        )
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.payrollPegawaiBpjsService
            .getPayrollPegawaiBpjs(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search, jenis_bpjs: this.jenis_bpjs
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }




    delete(id) {
        this.spinner.show();
        this.payrollPegawaiBpjsService.deletePayrollPegawaiBpjsId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }
}
