import { Component, OnInit } from '@angular/core';
import { GaKlaimPengajuanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-klaim',
  templateUrl: './klaim.component.html',
  styleUrls: ['./klaim.component.scss']
})
export class KlaimComponent implements OnInit {

    constructor(
        private gaKlaimPengajuanService: GaKlaimPengajuanService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
    ) {}

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    ngOnInit() {
        this.searchData()
    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.gaKlaimPengajuanService
            .getGaKlaimPengajuans(
                JSON.stringify({
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize,
                    sortKey: this.sortKey,
                    sortValue: this.sortValue,
                    search: this.search,
                }),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data)
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }


}
